import axios from 'axios'

function About(props) {
  return (
    <div>
      <div>About</div>
      <div> {props.value} </div>
    </div>
  );
}

About.getInitialProps = async () => {
  const res = await axios.get('https://binance-api-example.herokuapp.com/')
  const data = res.data
  console.log(data)
  return data.filter(val => val.slug === 'about')
};

export default About;
